// createRouter - 라우터 인스턴스 생성 함수
// createWebHistory - URL 히스토리 모드를 사용하기 위한 함수
import { createRouter, createWebHistory } from 'vue-router';

// import 컴포넌트명 from '경로'
import HomePage from '../views/Home.vue'
import AboutPage from '../views/About.vue'
import ProductPage from '../views/Product.vue'

// 라우트 설정을 정의하는 배열 - path, name, component
    // 라우터가 경로를 제대로 연결하지 못 하는 문제 발생 방지
const routes = [
    // Home 페이지
    {
        path: '/', // URL 경로 정의
        name: 'Home', // 라우터 고유이름 정의 - path의 경로 참조하여 이동 시 사용
        component: HomePage, // 렌더링할 컴포넌트 지정
    },
    // About 페이지
    {
        path: '/about', // 경로는 소문자로 지정 (일관성 유지)
        name: 'About',
        component: AboutPage,
    },
    // Product 페이지
    {
        path: '/product',
        name: 'Product',
        component: ProductPage,
    },
];

// createRouter - 얘가 생성하는 라우터 인스턴스는 라우터의 경로와 연결된 컴포넌트를 관리하고 뷰를 어떻게 렌더링할지 결정하는 역할
// process.env.Base_URL - history 모드를 사용해서 URL 처리
    // history 모드 - URL에 # 없이 깔끔한 URL 제공
    // BASE_URL - public/index에서 지정된 기본 URL
const router = createRouter({
    history: createWebHistory(process.env.Base_URL),
    routes,
});

export default router; // 라우터 내보내기